// Main
export const loginRouter = {
    path: '/login',
    name: 'login',
    meta: {
        title: ' -  '
    },
    component: () => import('@/views/login.vue')
};

export const registRouter = {
  path: '/regist',
  name: 'regist',
  meta: {
    title: ' - '
  },
  component: () => import('@/views/regist.vue')
};



export const registResult = {
    path: '/regist-result',
    name: 'regist-result',
    meta: {
        title: ' - '
    },
    component: () => import('@/views/regist-result.vue')
};

