import Vue from 'vue';
import iView from 'iview';
import Util from '../libs/util';
import VueRouter from 'vue-router';
import Cookies from 'js-cookie';
import { routers, otherRouter } from './router';
import store from '../store';
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

// Vue.use() 
Vue.use(VueRouter);
Vue.use(mavonEditor)

// 
const RouterConfig = {
    // mode: 'history',
    routes: routers
};

//  router  `RouterConfig` 
export const router = new VueRouter(RouterConfig);

/**
 *  router.beforeEach 
 *
 * to: Route:  
 *
 * from: Route: 
 *
 * next: Function:  resolve  next 
 * next():  confirmed ()
 * next(false):  URL  () URL  from 
 * next('/')  next({ path: '/' }):  next  replace: truename: 'home'  router-link  to prop  router.push 
 * next(error): (2.4.0+)  next  Error  router.onError() 
  next  resolved
 */
router.beforeEach((to, from, next) => {
    iView.LoadingBar.start();
    //title
    Util.title(to.meta.title);
    if (Cookies.get('locking') === '1' && to.name !== 'locking') {
        // 
        next({
            replace: true,
            name: 'locking'
        });
    } else if (Cookies.get('locking') === '0' && to.name === 'locking') {
        next(false);
    } else {
        if (!Cookies.get('userInfo') && to.name !== 'login' && to.name !== 'regist' && to.name !== 'regist-result') {
          // ,
            next({
                name: 'login'
            });
        } else if (Cookies.get('userInfo') && to.name === 'login') {
            // 
            Util.title();
            next({
                name: 'home_index'
            });
        } else {
            Util.toDefaultPage([...routers], to.name, router, next);
        }
    }
});

// next 
router.afterEach((to) => {
    Util.openNewPage(router.app, to.name, to.params, to.query);
    iView.LoadingBar.finish();
    window.scrollTo(0, 0);
});



